/* Importing the font from Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Tilt+Neon&display=swap');
@import 'index.css';
html {
  height: 100%;
  width: 100%;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

body {
  background: linear-gradient(323deg, #eaf28e, #b6f88a, #1747c9, #4568bc);
  background-size: 240% 240%;
  animation: gradient-animation 16s ease infinite;
}

@keyframes gradient-animation {
  0% {
    /* Specify the starting point of the animation */
    background-position: 0% 50%;
  }
  50% {
    /* Specify the midpoint of the animation */
    background-position: 100% 50%;
  }
  100% {
    /* Specify the ending point of the animation */
    background-position: 0% 50%;
  }
}

#head1 {
  color: white;
  font-family: 'Tilt Neon', cursive;
  font-size: 50px;
  text-shadow: 0 0 10px #f4d03f, 0 0 20px #f4d03f, 0 0 30px #f4d03f, 0 0 40px #f4d03f, 0 0 70px #f4d03f, 0 0 80px #f4d03f, 0 0 100px #f4d03f, 0 0 150px #f4d03f;
  animation: neon-glow 1.5s ease-in-out infinite alternate;
  margin-top: 10px;
}

@keyframes neon-glow {
  0% {
    text-shadow: 0 0 10px #f4d03f, 0 0 20px #f4d03f, 0 0 30px #f4d03f, 0 0 40px #f4d03f, 0 0 70px #f4d03f, 0 0 80px #f4d03f, 0 0 100px #f4d03f, 0 0 150px #f4d03f;
  }
  100% {
    text-shadow: 0 0 20px #f4d03f, 0 0 30px #f4d03f, 0 0 40px #f4d03f, 0 0 50px #f4d03f, 0 0 80px #f4d03f, 0 0 90px #f4d03f, 0 0 110px #f4d03f, 0 0 160px #f4d03f;
  }
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.input-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.input-container label {
  margin-right: 10px;
}

.input-container input {
  padding: 5px;
  border-radius: 5px;
  border: none;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
}

.button {
  padding: 10px 20px;
  background-color: #0078d4;
  color: white;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

.button:hover {
  background-color: #005a9e;
}

/* Styles for the login form */
#login-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px 70px 70px 70px;
  background-color: #4568bc6b;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  margin-top: 20px;
}

#login-form label {
  display: block;
  margin-bottom: 10px;
  color: white; /* Set the label color to white */
  font-size: 1.2em; /* Increase the font size */
}

#login-form input {
  width: 100%;
  padding: 8px;
  border-radius: 5px;
  border: none;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
  margin-bottom: 10px;
}

#login-form .button {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  letter-spacing: 1.5px;
  margin-top: 13%;
  color: white;
  background: darkcyan;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease; /* Add a smooth transition */
}

#login-form .button:hover {
  background-color: rgb(71, 170, 170); /* Change the button color on hover */
}

input#password,
input#username {
  width: 100%;
}
