body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

#topDiv{
  margin-left: 3%;
  display: flex;
  position: relative;
  color: white;
  font-family: 'Tilt Neon', cursive;
  font-size: 2rem;
  text-shadow: 0 0 10px #f4d03f, 0 0 20px #f4d03f, 0 0 30px #f4d03f, 0 0 40px #f4d03f, 0 0 70px #f4d03f, 0 0 80px #f4d03f, 0 0 100px #f4d03f, 0 0 150px #f4d03f;
  animation: neon-glow 1.5s ease-in-out infinite alternate;
  margin-top: 10px;
}

#user {
  position: absolute;
  top: 10px;
  right: 3px;
  width: 250px;
  padding: 30px 35px;
  background-color: #4568bc6b;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3); 
}

#logoutButton {
  margin-top: 15px;
  letter-spacing: 1.5px;
  display: block;
  text-align: center;
  color: white;
  border-radius: 5px;
  border: none;
  transition: background-color 0.3s ease;
  position: absolute;
  bottom: 10px;
  right: 10px;
}

#transactions {
  margin-left: 3%;
  margin-top: 1%;
  margin-right: 23%;
  border-radius: 10px;
  padding: 10px 15px 15px 15px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  background-color: #4568bc6b;
}

.TransChart-container {
  display: flex;
  flex-direction: column;
  align-items: center;

}

.TransChart-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
}

.chart {
  width: 80%;
  height: 80%;
  margin-left: 15%;
  margin-top: -12%;
}

#bottomDiv {
  display: flex;
  position: static;
  margin-top: 5%;
  margin-left: 40%;
  font-family: 'Tilt Neon', cursive;
  font-size: 2rem;
  color: white;
  text-shadow: 0 0 10px #f4d03f, 0 0 20px #f4d03f, 0 0 30px #f4d03f, 0 0 40px #f4d03f, 0 0 70px #f4d03f, 0 0 80px #f4d03f, 0 0 100px #f4d03f, 0 0 150px #f4d03f;
  animation: neon-glow 1.5s ease-in-out infinite alternate;
  margin-top: 10px;
}
#midDiv {
  display: flex;
  position: absolute;
  margin-top: 5%;
  margin-left: 80%;
  font-family: 'Tilt Neon', cursive;
  font-size: 2rem;
  color: white;
  text-shadow: 0 0 10px #f4d03f, 0 0 20px #f4d03f, 0 0 30px #f4d03f, 0 0 40px #f4d03f, 0 0 70px #f4d03f, 0 0 80px #f4d03f, 0 0 100px #f4d03f, 0 0 150px #f4d03f;
  animation: neon-glow 1.5s ease-in-out infinite alternate;
  margin-top: 10px;
}
#barChart {
  position: absolute;
  margin-left: 75%;
  margin-top: 10%;
}

